@import "../../utilities/variables.scss";

.Home {
  box-sizing: border-box;
  justify-content: space-between;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.Home-Background {
  display: none;
  -webkit-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
  opacity: 0.2;
  width: 100%;
  height: 100%;
  position: absolute;
}

.Home-Logo {
  img {
    width: 250px;
  }
}

.Home-Content-Container {
  transition: all 1s;
  padding-top: 5vh;
  border-width: 0px 0px 0px 0px;
  border-style: solid;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Header-Background {
  display: none;
  position: absolute;
  font-size: 20rem;
  white-space: nowrap;
  opacity: 0.05;
  -webkit-filter: blur(7px);
  pointer-events: none;
}

.Painting-Button {
  margin-top: 1.75em;
  position: relative;

  a {
    display: flex;
    align-items: center;
    width: fit-content;
    transition: transform 250ms;

    &:hover {
      transform: scale(1.1);
    }
  }

  h3 {
    text-transform: uppercase;
    font-size: 1.25em;
  }

  svg {
    transform: rotate(180deg);
    height: 30px;
  }
}

.Bottom-Bar {
  padding-bottom: 30px;
  margin-top: 50px;
  opacity: 0.3;
  font-weight: bold;
  display: flex;
  justify-content: space-between;

  .Bottom-Bar-Links {
    display: flex;
    gap: 10px;
  }
}

.Home-Title {
  position: relative;

  p {
    white-space: wrap;
    max-width: 600px;
  }

  h1 {
    background: -webkit-linear-gradient(rgb(127, 20, 133), rgb(82, 41, 170));
    -webkit-background-clip: text !important;
    -webkit-text-fill-color: transparent !important;
    color: black;
    width: fit-content;
    white-space: nowrap;
  }
}

.Home-Swiper {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.swiper {
  margin: 0;
  overflow: visible;
}

.swiper-wrapper {
}

.swiper-slide {
  border-radius: 5px;
  overflow: hidden;
  max-width: 80vw;
  z-index: 0;
  position: relative;
  min-width: 1px;
  max-width: 80vw;
  width: auto;
  height: 40vh;
  transition: opacity 1s !important;
  transition: transform 500ms;
  -webkit-filter: saturate(0);
  opacity: 0.25;

  @media (max-width: $md) {
  }

  &.swiper-slide-active {
    -webkit-filter: saturate(1) blur(0px);
    border: solid rgb(255, 255, 255);
    border-width: 0px 0px;
    box-sizing: content-box;
    z-index: 100;
    opacity: 1;

    &:hover {
    }

    img {
      opacity: 1;
    }
  }

  img {
    opacity: 1;
    height: 100%;
    object-position: center;
    object-fit: cover;
  }
}

.swiper-navigation {
}

.swiper-button-next {
  right: 35px;
}

.swiper-button-prev {
  left: 35px;
}

.swiper-button-next,
.swiper-button-prev {
  color: rgb(109, 109, 109);
}

.custom-pagination {
  margin-top: 20px;
  bottom: 0px !important;
  position: relative;

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }
  .swiper-pagination-bullet-active {
    background-color: rgb(134, 134, 134);
  }
}
